@media (max-width: 1366px) {
}

@media (max-width: 1024px) {
	header {
		position: absolute;
		.header-section .header-nav {
			.header-nav__ul {
				&-li {
					&--link {
						display: none;
						&.logo {
							display: block;
						}
					}
				}
			}
			.logo::after {
				left: 50%;
				transform: translateX(-50%);
			}
		}
	}
	.tablet-burger {
		display: block;
	}
	.posts {
		&Item {
			flex-basis: 50%;
			max-width: 50%;

			&:nth-last-child(2) {
				border-right: 0;
			}

			&:last-child {
				display: none;
			}
		}
	}
	.latest-posts {
		.latest-posts-column {
			flex-basis: 50%;
			max-width: 50%;

			&:first-child {
				border-right: 1px solid #000;
				margin-bottom: 40px;
			}

			&:nth-child(2) {
				border-right: none;
				margin-bottom: 40px;
			}
		}
	}

	footer .subscribe-section .subscribe {
		&Item {
			&:first-child,
			&:nth-child(2) {
				flex-basis: 50%;
				max-width: 50%;
			}

			&:nth-child(3) {
				flex-basis: 100%;
				max-width: 100%;
			}
		}
	}

	.default-section {
		width: 100%;
	}

	.page-title-wrap {
		.page-title {
			&__text {
				font-size: 280px;
			}
		}

		.single-page-title {
			&__text {
				flex-basis: 100%;
				max-width: 100%;
				font-size: 50px;
				line-height: normal;
			}
		}
	}
}

@media (max-width: 767px) {
	.tablet-burger {
		display: none;
	}
	.container {
		padding-left: 20px;
		padding-right: 20px;
	}
	.home-slider-section {
		.home-slider {
			.owl-nav {
				.owl-next,
				.owl-prev {
					top: 20%;
				}

				.owl-next {
					right: 0px;
				}

				.owl-prev {
					left: 0px;
				}
			}
			&Item {
				text-align: center;

				&__content,
				&__image {
					flex-basis: 100%;
					max-width: 100%;
				}
				&__lead {
					display: none;
				}
				&__title {
					font-size: 40px;
					padding: 0 20px;
				}
				&__image {
					min-height: 300px;
					filter: none;
				}
			}
		}
	}
	section {
		padding-bottom: 0;
	}
	.posts {
		&Item {
			flex-basis: 100%;
			max-width: 100%;
			border-right: 0;
			margin-bottom: 40px;

			&__image {
				filter: none;
			}
		}
	}
	.latest-section {
		.latest-posts {
			.latest-posts-column {
				flex-basis: 100%;
				max-width: 100%;
				border-right: 0;
				margin-bottom: 0;
			}
			&Item {
				border-bottom: 1px solid $color-black;
			}
		}
	}
	footer {
		.subscribe-section {
			.subscribe {
				&Item {
					flex-basis: 100% !important;
					max-width: 100% !important;
				}
				&__title {
					padding-right: 0;
					text-align: center;
				}
			}
		}
	}

	.page-title-wrap {
		.page-title {
			height: auto;
			line-height: 90px;
			padding-top: 60px;
			padding-bottom: 60px;

			&__text {
				font-size: 100px;
			}
		}
	}

	.mobile-burger {
		display: block;
	}

	header .header-section .header-nav .logo:after {
		filter: none;
	}
}
