*,
*::before,
*::after {
	box-sizing: inherit;
}

html {
	box-sizing: border-box;
}

body {
	font-family: $font-conqueror-slab, serif;
	font-size: 16px;
	line-height: 1.4;
	text-rendering: optimizeLegibility;
}

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

ul {
	padding: 0;
	margin: 0;

	li {
		list-style: none;
	}
}

a {
	text-decoration: none;
}

.container {
	width: auto;
	max-width: 1300px;
	float: none;
	display: block;
	margin-right: auto;
	margin-left: auto;
	padding-left: 64px;
	padding-right: 64px;
}

.container:before,
.container:after {
	content: '';
	display: table;
}

@import 'partials/header';
@import 'partials/slider';
@import 'partials/posts';
@import 'partials/footer';
@import 'partials/page-title';

.loader-section {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 9999;
	display: flex;
	justify-content: center;
	align-items: center;
	width: 100%;
	height: 100%;
	background: $color-white;

	.polygon-loader {
		position: absolute;
		height: 192px;
		width: 128px;

		div {
			position: absolute;
			top: 64px;
			left: 64px;
			border: 0 solid transparent;
			transform-origin: left top;
			animation-duration: 0.52s;
			animation-iteration-count: infinite;
			animation-direction: alternate;
			animation-timing-function: steps(4);

			&:nth-of-type(1) {
				border-top-color: #210008;
				transform: rotate(-180deg);
				animation-name: type-01;
			}
			&:nth-of-type(2) {
				border-top-color: #520019;
				transform: rotate(-90deg);
				animation-name: type-01;
				animation-delay: 0.08s;
			}
			&:nth-of-type(3) {
				border-top-color: #7b0029;
				transform: rotate(0);
				animation-name: type-01;
				animation-delay: 0.12s;
			}
			&:nth-of-type(4) {
				border-top-color: #a5003a;
				transform: rotate(90deg);
				animation-name: type-01;
				animation-delay: 0.16s;
			}
			&:nth-of-type(5) {
				left: 0;
				border-top-color: #d6004a;
				transform: rotate(45deg);
				animation-name: type-02;
				animation-delay: 0.24s;
			}
			&:nth-of-type(6) {
				top: 128px;
				left: 0;
				border-top-color: #ff0052;
				transform: rotate(0);
				animation-name: type-03;
				animation-delay: 0.28s;
			}
		}
	}

	@keyframes type-01 {
		0% {
			border-top-width: 0;
			border-right-width: 64px;
			border-bottom-width: 64px;
			border-left-width: 0;
		}
		40% {
			border-top-width: 0;
			border-right-width: 64px;
			border-bottom-width: 64px;
			border-left-width: 0;
		}
		45% {
			border-top-width: 64px;
			border-right-width: 64px;
			border-bottom-width: 64px;
			border-left-width: 0;
		}
		100% {
			border-top-width: 64px;
			border-right-width: 64px;
			border-bottom-width: 64px;
			border-left-width: 0;
		}
	}
	@keyframes type-02 {
		0% {
			border-top-width: 0;
			border-right-width: 46px;
			border-bottom-width: 46px;
			border-left-width: 46px;
		}
		40% {
			border-top-width: 0;
			border-right-width: 46px;
			border-bottom-width: 46px;
			border-left-width: 46px;
		}
		45% {
			border-top-width: 46px;
			border-right-width: 46px;
			border-bottom-width: 46px;
			border-left-width: 46px;
		}
		100% {
			border-top-width: 46px;
			border-right-width: 46px;
			border-bottom-width: 46px;
			border-left-width: 46px;
		}
	}
	@keyframes type-03 {
		0% {
			border-top-width: 0;
			border-right-width: 0;
			border-bottom-width: 64px;
			border-left-width: 64px;
		}
		40% {
			border-top-width: 0;
			border-right-width: 0;
			border-bottom-width: 64px;
			border-left-width: 64px;
		}
		45% {
			border-top-width: 64px;
			border-right-width: 0;
			border-bottom-width: 64px;
			border-left-width: 64px;
		}
		100% {
			border-top-width: 64px;
			border-right-width: 0;
			border-bottom-width: 64px;
			border-left-width: 64px;
		}
	}
}

.main {
	margin-top: 115px;

	body.inner-page & {
		margin-top: 40px;
	}
}

.default-section {
	width: 75%;
	margin: 0 auto;
	padding-bottom: 40px;
	font: 18px $font-conqueror-slab, serif;
	line-height: 24px;

	img {
		max-width: 100%;
		margin-bottom: 15px;
	}

	h1,
	h2,
	h3,
	h4,
	h5,
	h6 {
		margin-bottom: 10px;
		font-family: $font-theodore-glagolev-display, Arial, sans-serif;
		font-feature-settings: 'clig' 0, 'calt' 0, 'case' 1;
		font-kerning: normal;
		font-variant-ligatures: common-ligatures;
		text-transform: uppercase;
		color: $link-hover;
	}

	h1 {
		font-size: 55px;
		line-height: 70px;
	}

	h2 {
		font-size: 50px;
		line-height: 60px;
	}

	h3 {
		font-size: 45px;
		line-height: 50px;
	}

	p {
		margin-bottom: 10px;
	}
}

section {
	padding-bottom: 40px;
	margin-bottom: 40px;
	border-bottom: 2px solid $color-black;

	&.border-none {
		border-bottom: 0;

		body.inner-page & {
			padding-bottom: 0;
		}
	}

	.section-title {
		position: relative;
		margin-bottom: 40px;
		text-align: center;

		&__text {
			position: relative;
			display: inline-block;
			z-index: 1;
			padding: 0 30px;
			font: 32px $font-reforma, Arial, sans-serif;
			letter-spacing: 3px;
			text-transform: uppercase;
			background-color: #fff;
		}

		&:after {
			content: '';
			position: absolute;
			left: 0;
			top: 50%;
			width: 100%;
			height: 2px;
			background-color: $color-black;
		}
	}

	&:last-child {
		border-bottom: 0;
	}
}

.tablet-burger {
	display: none;
	position: fixed;
	top: 20px;
	right: 0;
	z-index: 15;

	button {
		&:focus {
			outline: none;
			background: transparent;
		}
	}
}

.mobile-burger {
	position: fixed;
	bottom: 0;
	z-index: 15;
	width: 100%;
	background: $color-white;
	text-align: right;

	button {
		display: flex;
		align-items: center;
		justify-content: center;
		width: 100%;
		font: 32px $font-reforma, Arial, sans-serif;
		letter-spacing: 3px;
		text-transform: uppercase;
		box-shadow: 0 -3px 5px rgba(57, 63, 72, 0.3);

		.hamburger-box {
			margin-right: 10px;
		}

		&:focus {
			outline: none;
			background: transparent;
		}
	}
}

.responsive-menu {
	z-index: 14;
	position: fixed;
	top: 0;
	left: 0;
	display: none;
	width: 100%;
	height: 100%;
	background: $color-white;

	.responsive-nav {
		padding-top: 70px;
		font: 22px $font-reforma, Arial, sans-serif;
		letter-spacing: 2px;

		&__ul {
			display: flex;
			flex-wrap: wrap;
			flex-direction: column;

			&-li {
				padding: 8px 0;
				text-align: center;
				&--link {
					color: $color-black;
					transition: all 0.4s ease;

					&:hover {
						color: $header-hover;
					}
				}
			}
		}
	}
}

.right-arrow,
.left-arrow {
	display: inline-block;
	height: 38px;
	width: 28px;
	background-image: url(../images/sprites/svg/arrow.svg);
	background-repeat: no-repeat;
}

.left-arrow {
	transform: scaleX(-1);
}
